import {
  Streamlit,
  withStreamlitConnection,
  StreamlitComponentBase
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
import ReactJson from 'react-json-view'



class JsonViewer extends StreamlitComponentBase {

  public render = (): ReactNode => {

    const metadata = this.props.args["jsonMetadata"]

    return (
      <div style={{minHeight: "800px", paddingBottom:"20px"}} onClick={() => Streamlit.setComponentValue(-1)} >
        <ReactJson src={JSON.parse(metadata)} name={false} iconStyle={"triangle"} collapsed={2}/>
      </div>
        )
  }
}

export default withStreamlitConnection(JsonViewer)

